<template>

<div>

  <div v-if="data.data.source === 'youtube'" class="in-article-video youtube">
    <svg class="space-holder" viewBox="0 0 350 200"></svg>
    <iframe class="yt-video" width="350" height="200" :src="'https://www.youtube.com/embed/' + data.data.remote_id" frameborder="0" allowfullscreen></iframe>
  </div>

  <div v-if="data.data.source === 'kaltura'" class="in-article-video kaltura">
    <svg class="space-holder" viewBox="0 0 350 200"></svg>
    <iframe class="k-video" :src="kalturaSrc" width="988" height="586" allowfullscreen webkitallowfullscreen mozAllowFullScreen frameborder="0" itemprop="video" itemscope itemtype="http://schema.org/VideoObject"></iframe>
  </div>

</div>

</template>

<style lang="scss" scoped>

  @import "../../assets/sass/app.scss";

 .in-article-video {
   @include gutter;
   position: relative;
   margin-bottom: $bottom;
   svg {
     width: 100%;
     height: auto;
     max-height: 75vh;
   }
   iframe.yt-video,
   iframe.k-video
    {
     position: absolute;
     top: 0;
     left: 0;
     width: 1px;
     min-width: 100%;
     height: 1px;
     min-height: 100%;
   }

 }

</style>

<script>
    export default {
      created() {
        window.__inarticlevideo = this
      },
      data(){
        return {
          kalturaSrc: "//cdnapi.kaltura.com/p/676152/sp/67615200/embedIframeJs/uiconf_id/8168832/partner_id/676152?iframeembed=true&playerId=kaltura_player_1510845780&entry_id="+this.data.data.remote_id+"&flashvars[streamerType]=auto"
        }
      },
      props: [
        'data',
      ]
    }
</script>



