$(function(){

  var nav_opened = false,
  	  nav_animating = false,
  $mask = $(".white-mask");
  $(".menu-ico").click(function(){
	  if (!nav_animating){
		  if (!nav_opened){
			  nav_opened = true;
			  nav_animating = true;
				if ($(window).width() > 1024){
					disableScroll();
				} else {
					$("body, html").toggleClass("no-scroll");
				}
			  $(".header__nav").css({display:"block"});
			  $mask.velocity({ height:["400%","10px"]},899, function(){

			  });
			  setTimeout(function(){
				  $(".header__nav").toggleClass("active");
				  $("body").toggleClass("nav-active");
				  $(".menu-ico").toggleClass("active");
				  nav_animating = false;
			  },400);
		  } else {
			  nav_opened = false;
			  nav_animating = true;
			  setTimeout(function(){
				  $(".header__nav").toggleClass("active");
				  $("body").toggleClass("nav-active");

				  $(".menu-ico").toggleClass("active");
			  }, 350);
			  $mask.velocity({height:["10px","400%"]}, 899, function(){
				  enableScroll();
				  $("body, html").removeClass("no-scroll");
				  $(".header__nav").css({display:"none"});
				   nav_animating = false;
			  });
		  }
	  }
  });

  $(".bck-to-top").click(function(){
    $("html, body").animate({"scrollTop":0},500);
  });

  if($(window).width()<1024){
    $('.header__nav__menu-parent').click(function(){
      $(".header__submenu").toggleClass("active");
      $(".submenu-arrow").toggleClass("active");
    });
  }
});

var resize_timeout;
$( window ).resize(function() {
  clearTimeout(resize_timeout);
  resize_timeout = setTimeout(function(){
    gridRelocate();
  },300)
});

function gridRelocate(){
  var width = $(window).width(),
      grid_width = 2800,
      left = (width - grid_width) /2;
  $(".b-grid__holder").animate({left:left+'px'},400);
}
function gridInit(){
  var width = $(window).width(),
      grid_width = 2800,
      left = (width - grid_width) /2;
      console.log(left);
  $(".b-grid__holder").css({"left":left+'px'});
}
var keys = {37: 1, 38: 1, 39: 1, 40: 1};

function preventDefault(e) {
  e = e || window.event;
  if (e.preventDefault)
      e.preventDefault();
  e.returnValue = false;
}

function preventDefaultForScrollKeys(e) {
    if (keys[e.keyCode]) {
        preventDefault(e);
        return false;
    }
}

function disableScroll() {
  if (window.addEventListener) // older FF
      window.addEventListener('DOMMouseScroll', preventDefault, false);
  window.onwheel = preventDefault; // modern standard
  window.onmousewheel = document.onmousewheel = preventDefault; // older browsers, IE
  window.ontouchmove  = preventDefault; // mobile
  document.onkeydown  = preventDefaultForScrollKeys;
}

function enableScroll() {
    if (window.removeEventListener)
        window.removeEventListener('DOMMouseScroll', preventDefault, false);
    window.onmousewheel = document.onmousewheel = null;
    window.onwheel = null;
    window.ontouchmove = null;
    document.onkeydown = null;
}



