<template>
  <div id="page--showcase">

    <h1 id="page-title">{{name}}</h1>
    <h4 id="page-preamble">Small descrioton on what can be foound on this section Lorem ipsum dolor sit amet, consectetur adipiscing elit. Etiam quis odio ante.</h4>
    <Filters />
    <History ref='history' />
    <Articles :query="query" />
  </div>
</template>



<script>

import Vue from 'vue'
import Articles from '@/components/blocks/Articles'
import History from '@/components/blocks/History'
import Filters from '@/components/blocks/Filters'


export default {

  created() {
    window.__showcase = this
  },

  data () {
    return {
      name: this.$route.name
    }
  },

  components: {
    Articles,
    History,
    Filters
  },

  props: [
    'query',
    //'params',
  ],

  beforeRouteLeave (to, from, next) {
    this.$refs.history.historyAdd(to.params.id);
    if (!!to.params.external_url) {
      this.$refs.history.updateHistory();
      window.open(to.params.external_url, '_blank')
    }
    else {
      next();
    }
  },

}

</script>



