export default {

  methods: {

    articleImages: function(article) {
      return article.elements.reduce(function(_imgs, element) {
        switch (element.type) {
          case 'diwanee_image' :
          _imgs.diwanee_image.push( element.data.file.url );
          break;
          case 'slider_image' :
          _imgs.slider_image.push( element.data.file.url );
          break;
          case 'diwanee_video' :
            if (element.data.source == "youtube") {
              _imgs.video_youtube.push( element.data.remote_id );
            }
            if (element.data.source == "kaltura") {
              _imgs.video_kaltura.push( element.data.remote_id );
            }
          break;
        }
        return _imgs
      }, {
        diwanee_image:[],
        slider_image:[],
        video_youtube:[],
        video_kaltura:[]
      });
    },

    setDataImg: function(article) {
      var imgs = this.articleImages(article);
      if (!!imgs.diwanee_image[0]) article.img = imgs.diwanee_image[0];
      else if (!!imgs.slider_image[0]) article.img = imgs.slider_image[0];
      else if (!!imgs.video_youtube[0]) article.img =  'https://img.youtube.com/vi/'+imgs.video_youtube[0]+'/hqdefault.jpg';
      else if (!!imgs.video_kaltura[0]) article.img =  'http://www.kaltura.com/p/676152/thumbnail/entry_id/'+imgs.video_kaltura[0]+'/width/800/height/600/a.jpg';
      else article.img =  "http://via.placeholder.com/350x200";
    },

    historyAdd: function(id) {
      var history = JSON.parse(localStorage.getItem("browserHistory") || "[]");
      var position = history.indexOf(id);
      if (position !== -1) {
        history.splice(position, 1);
      }
      history.unshift(id);
      localStorage.setItem("browserHistory", JSON.stringify(history));
    },

  },

};



