<template>

  <div id="history">

    <div id="history-line" >
      <div id="history-line-in" >
        <a id='history-back' href="#" v-on:click.prevent="back"><img class='back-img' src="../../assets/img/left-arrow.svg"/></a>
        <a id='history-open' href="#" v-on:click.prevent="open" v-if="historyExist">browse history</a>
      </div>
    </div>

    <aside id="history-driver" class='closed test'>
      <h2>History</h2>
      <article v-for="article in articles">
        <router-link :to="{name: 'Article', params: {id: article.id, external_url: article.external_url}}">
          <h4>{{article.title}}</h4>
          {{setDataImg(article)}} <!-- opens posibility for asinc image (e.g. kaltura poster cover) -->
          <div class="img-w">
            <img :src=article.img alt='' />
          </div>
        </router-link>
      </article>
      <a id="history-clear" href="#" v-on:click.prevent="clear" v-if="historyExist">clear history</a>
      <a id="history-close" href="#" v-on:click.prevent="close">
        <svg viewBox="0 0 40 40">
          <path stroke="black" stroke-width="2" d="M 0,0 L 40,40 M 40,0 L 0,40" />
        </svg>
      </a>
    </aside>
  </div>

</template>


<style lang="scss" scoped>

@import "../../assets/sass/app.scss";

#history-line {
  //background-color: $painted;
  @include device(mobile) {
    display: none;
  }
  #history-line-in {
    @include gutter;
    position: relative;
    //overflow: hidden;
    #history-open {
      $color: gray;
      float: right;
      width: 160px;
      max-width: 17%;
      padding: 0 .25em;
      height: 2.5rem;
      line-height: 2.5rem;
      //vertical-align: sub;
      text-align: center;
      border: 1px solid $color;
      text-decoration: none;
      font-family: $sans;
      font-size: 1rem;
      color: $color;
      font-weight: 400;
      overflow: hidden;
      margin-top: 0rem;
    }

    #history-back {
      img {
        visibility:hidden;
        width: 35px;
        padding: .5rem 0;
        display: block;
        float: left;
      }
    }

  }
}

#history-driver {
  position: fixed;
  right: 0;
  top: 0;
  height: 100%;
  width: 33%;
  max-width: 450px;
  z-index: 100;
  background-color: white;
  outline: 1px solid red;
  margin: 1px;
  overflow: auto;
  transition: all .4s;
  &.closed {
    right: -100%;
  }
  #history-close {
    position: absolute;
    top: 0;
    right: 0;
    width: 50px;
    height: 50px;
    padding: 18px;
    svg {
      width: 100%;
      height: 100%;
      path {
        stroke-width: 5;
      }
    }
  }
  h2 {
    font-size: 2.2rem;
    text-align: center;
    margin: 0;
    padding: 3rem;
  }
  article {
    width: 52%;
    margin: 0 24%;
    padding-top: 5rem;
    //height: 16rem;
    a {
      text-decoration: none;
      color: gray;
    }
    h4 {
      text-align: center;
      font-family: $sans;
      margin: 0 0 .6rem;
      font-weight: 300;
      font-size: 1.2rem;
    }
    .img-w {
      padding-bottom: #{ (200/350) * 100%};
      overflow: hidden;
      position: relative;
      margin-bottom: 0.9rem;
      img {
        position: absolute;
        display: block;
        top: 0;
        left: 0;
        width: 100%;
      }
    }
  }
  #history-clear {
    color: gray;
    text-decoration: none;
    display: block;
    margin: 8rem auto 0;
    text-align: center;
    opacity: 0.2;
  }
}

</style>




<script>

export default {

  created() {
    window.__history = this;
  },

  activated() {
    this.updateHistory();
  },

  data () {
    return {
      articles: [],
      lastUrl: ""
    }
  },

  computed: {

    historyExist: {
      cache: false,
      get() {
        return !!this.history.length;
      }
    },

    history: {
      cache: false, // !!! FCK
      set: function(history) {
        localStorage.setItem("browserHistory", JSON.stringify(history));
      },
      get: function() {
        return JSON.parse(localStorage.getItem("browserHistory") || "[]")
      },
    },

  },

  props: [
    'id',
    'historyTimestamp'
  ],

  methods: {
    updateHistory(){
      var _this = this;
      //check whether history is empty
      if (!_this.historyExist) {
        _this.articles = [];
        return; // history is empty
      }
      //if history not empty fetch
      var url = window.apiUrl+"articles?ids=" + _this.history.toString();
      //if (url === _this.lastUrl) {
      //  return; // cache due to no history change
      //}
      //_this.lastUrl = url;
      this.$http.get(url).then(function(response){
        _this.articles = response.data;
      });
    },

    open(){
      document.getElementById('history-driver').classList.remove('closed')
    },
    close(){
      document.getElementById('history-driver').classList.add('closed')
    },
    clear(){
      localStorage.removeItem('browserHistory');
      this.articles = [];
      this.close();
    },
    back(){
      //this.$router.go(-1);
    }

  }



}

</script>



